.device-types {
  margin: $container-padding-lg 0;
  padding: 0 $container-padding;

  @media (min-width: $screen-lg) {
    margin-left: $menu-width;
    padding: 0 $container-padding-md;

    .swiper {
      display: none;
    }
  }

  &__title-block {
    width: 100vw;
    height: 80px;
    margin-left: -16px;
    margin-right: -16px;
    background: linear-gradient(to right, #f8f8f8 0%, #ffffff 100%);
    display: flex;
    align-items: center;
    padding: 0 16px;
    margin-bottom: 16px;
  }

  &__title {
    font-size: 16px;
    font-weight: 400;
    color: $color-grey;
  }

  &__list {
    display: flex;
    gap: 16px;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    scrollbar-width: none;
    -ms-overflow-style: none;
    padding-bottom: 16px;
    justify-content: space-between;

    @media (min-width: 1020px) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      overflow-x: visible;
      padding-right: 0;
    }

    @media (min-width: 1120px) {
      grid-template-columns: repeat(3, 1fr);
    }

    @media (min-width: 1360px) {
      grid-template-columns: repeat(4, 1fr);
    }

    &::-webkit-scrollbar {
      display: none;
    }

    @media (max-width: $screen-lg) {
      display: none;
    }
  }

  &__item {
    width: 240px;
    height: 160px;
    background: #fff;
    border: 1px solid $color-border;
    border-radius: 6px;
    padding: 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-shrink: 0;
    font-weight: 500;
    transition: opacity 0.3s ease-in-out;

    &--hidden {
      display: none;
    }
  }

  &__btn {
    border: none;
    background: none;
    width: 40px;
    height: 40px;
    margin-left: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: opacity 0.2s;

    &:hover {
      opacity: 0.7;
    }
  }

  &__text {
    font-size: 16px;
    color: $color-text;
    margin: 0;
  }

  &__button {
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
    align-self: flex-end;
    color: $color-text;
    font-size: 16px;
    transition: opacity 0.2s;

    &:hover {
      opacity: 0.7;
    }
  }

  &__show-all {
    display: none;
    background: none;
    color: black;
    border: none;
    font-family: 'TT Lakes';
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: opacity 0.2s;
    align-items: center;
    gap: 8px;
    margin-top: 16px;

    img {
      order: -1;
    }

    @media (min-width: 1020px) {
      display: flex;
    }
  }

  &__hide-all {
    display: none;
    background: none;
    color: black;
    border: none;
    font-family: 'TT Lakes';
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: opacity 0.2s;
    align-items: center;
    gap: 8px;
    margin-top: 16px;

    img {
      order: -1;
      transform: rotate(180deg);
    }

    @media (min-width: 1020px) {
      display: none;
    }
  }

  .swiper {
    position: relative;
    overflow: visible;
    width: 240px;
    height: 190px;
    margin-left: 0;
    padding-left: 0;
  }

  .swiper-wrapper {
    width: 100%;
    height: 160px;
    overflow: visible;
    cursor: grab;
    margin-left: 0;
    padding-left: 0;
  }

  .swiper-pagination.swiper-pagination-clickable.swiper-pagination-bullets.swiper-pagination-horizontal {
    position: absolute;
    top: 86px;
    left: 50vw;
    transform: translateX(-50%);
    padding-left: 0px;
  }

  .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    background-color: #b5b6bc;
    border-radius: 50%;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &-active {
      background-color: #444;
    }
  }

  .swiper-pagination.swiper-pagination-clickable.swiper-pagination-bullets.swiper-pagination-horizontal {
    position: absolute;
    top: 180px;
    left: 50vw;
    transform: translateX(-50%);
    padding-left: 0px;
  }
}
