.menu {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: $menu-width;
  background: #fff;
  z-index: 100;
  transform: translateX(-100%);
  transition: transform 0.3s;
  font-family: $font-main;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.05);

  &--visible {
    transform: translateX(0);
  }

  @media (min-width: $screen-lg) {
    transform: none;
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    width: $sidebar-width;
    z-index: 10;
    display: flex;
    flex-direction: column;

    &__close {
      display: none;
    }
  }

  &__btn-block {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 16px 24px 21px 24px;

    @media (min-width: $screen-lg) {
      margin: 30px 29px 37px 22px;
    }
  }

  &__icon-right {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  &__close,
  &__search {
    width: 40px;
    height: 40px;
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
    opacity: 1;
    transition: opacity 0.2s;

    &:hover {
      opacity: 0.7;
    }
  }

  &__logo {
    img {
      width: 96px;
      height: 50px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 24px;
    list-style: none;
    padding: 0;
    margin: 0;
    margin-bottom: 200px;

    @media (max-height: 768px) {
      gap: 0;
    }
  }

  &__highlights {
    display: flex;
    position: relative;
    width: 100%;
    align-items: center;
  }

  &__highlight {
    position: absolute;
    width: 4px;
    height: 32px;
    background-color: #41f6d7;
    border-radius: 0 20px 20px 0;
    top: 50%;
    transform: translateY(-50%);
  }

  &__item {
    font-size: 16px;
    color: $color-text;
    cursor: pointer;
    transition: color 0.2s;
    padding: 0 $container-padding-md;
    position: relative;
    font-weight: 500;
    display: flex;
    align-items: center;
    height: 32px;

    @media (min-width: $screen-md) {
      padding: 0 $container-padding-md;
    }

    &:hover {
      color: $color-grey;
    }

    &--active {
      color: $color-grey;
    }
  }

  &__contacts {
    position: fixed;
    bottom: $container-padding-lg;
    left: $container-padding-md;
    display: flex;
    flex-direction: column;
    gap: $container-padding;
    width: calc($menu-width - $container-padding-md * 2);
  }

  &__btm-icons {
    display: flex;
    gap: 16px;
  }

  &__icon {
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    opacity: 1;
    transition: 0.2s;

    &:hover {
      opacity: 0.7;
    }
  }

  &__mail {
    font-size: 16px;
    color: $color-text;
    margin-top: $container-padding;
  }

  &__phone {
    font-size: 24px;
    font-weight: 700;
  }

  &__lang {
    display: flex;
    gap: 7px;
  }

  &__lang-link {
    color: $color-text;
    text-decoration: none;
    font-size: 16px;

    &--active {
      color: $color-grey;
    }
  }
}
